@import '../core/var';

.plcard {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 320px;
  width: 90%;
  gap: 5px;
  padding: 20px 15px;
  border-radius: 20px;
  background-color: $white;
  &__autor {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    align-items: center;
    font-family: $font-C;
    &--image {
      width: 70.5px;
      height: 70.5px;
      background-position: center;
      background-size: cover;
      border-radius: 50%;
    }
    &--job {
      word-break: break-word;
      font-size: 1rem;
      text-align: center;
      font-family: $font-C;
    }
    &--name {
      word-break: break-word;
      font-size: 1.4rem;
      font-weight: bold;
      text-align: center;
    }
  }
  &__infoProject {
    width: 70%;
    &--subtitle {
      word-break: break-word;
      text-align: center;
      font-size: 1rem;
      font-family: $font-P;
    }
    &--title {
      word-break: break-word;
      font-size: 1.4rem;
      font-family: $font-P;
    }
    &--slogan {
      word-break: break-word;
      font-size: 1.2rem;
      font-family: $font-P;
    }
    &--desc {
      word-break: break-word;
      font-size: 0.8rem;
      margin: 14px 0 18px;
      font-family: $font-C;
      min-height: 48px;
    }
  }
  &__technologies {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $font-C;
    font-size: 1rem;
    word-break: break-word;
    &--icons {
      display: flex;
      gap: 5px;
      font-size: 1.5rem;
      padding-right: 10px;
      .icons__link {
        color: $dusk;
        &:hover {
          color: orange;
        }
      }
    }
  }
}
